//import $ from "jquery";

window.jQuery = window.$ = $; 


// Main JS file

var ME = {};

(function($) {

	ME._init = function() {	    
    ME.startListeners();  
	}
  
  ME.startListeners = function() {         
    

    let slides = gsap.utils.toArray(".slide");
    
    let container = document.getElementById("#group1"); 
    
    slides.shift(); // leave the first slide alone (remove it from the Array)
        
    let section = document.querySelector("#group1"),
      
    tl = gsap.timeline({
        scrollTrigger: {
          trigger: "#group1",    
          endTrigger: "#footer", // change this to match whatever is coming after the slides
          start: "top top",
          end: "top bottom",
          scrub: true,
          pin: true,
          pinSpacing: false,
        },
        defaults: {ease: "none"}
    });
      
    slides.forEach((slide, i) => {
      if ( i == 1) { // check if first slide
        tl.fromTo(slide, {
          xPercent: 100,
          x: 0,
        }, {
          xPercent: 0,
        })
        .fromTo(slide.querySelector(".slide-wrapper"), {
          xPercent: -100,
          x: 0
        }, {
          xPercent: 0,
        }, "<");
      }
      else { // check if not first slide
        tl.fromTo(slide, {
          xPercent: -100,
          x: 0,
        }, {
          xPercent: 0,
        })
        .fromTo(slide.querySelector(".slide-wrapper"), {
          xPercent: 100,
          x: 0
        }, {
          xPercent: 0,
        }, "<");
      }
      
    });


  	}
    
   
    const animateText = () => {
        setTimeout(async () => {
           gsap.to("h1", {y: "0%", duration: 0.5})
           //gsap.to(".introText", {y: "0%", duration: 0.4, delay: 0.8})  
        }, [2000])
    }
    
    animateText();    
  

})($);

$().ready(function() {
	ME._init(); 
});

